import React from 'react';
import colorTokens from './merged-colors.json';

const tokens = colorTokens.color.t1;

const DesignTokensDisplay = () => {

  return (
    <div>
      <h2>Color Tokens</h2>
      <table style={{ width: '100%', borderCollapse: 'collapse', marginTop: '10px' }}>
        <thead>
          <tr>
            <th>Token Name</th>
            <th>Light Value</th>
            <th>Dark Value</th>
          </tr>
        </thead>
        <tbody>
          {Object.entries(tokens).map(([tokenName, tokenData]) => (
            <tr key={tokenName}>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}>{tokenName}</td>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}>{tokenData.value}</td>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}><div class="preview" style="background-color:{tokenData.value}"></div></td>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}>{tokenData.darkValue}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};


export default DesignTokensDisplay;